.feature-popup {
    position: absolute;
    z-index: $overlay-index;
    padding: 5px;
    font-size: 10px;
    filter: alpha(opacity=80);
    background-repeat: no-repeat;
    background-image: url(../../images/tipsy.gif);
    background-position: top center;
}

.feature-popup-inner {
    padding: 5px 8px 4px 8px;
    background-color: black;
    color: white;
}

.zoom-area {
    position: absolute;
    top: 0px;
    background-color: #ccf;
    opacity: 0.5;
    z-index: $overlay-index;
}

.mouse-pos {
    position: absolute;
    top: 0px;
    background-color: black;
    opacity: 0.15;
    height: 100%;
    width: 1px;
}

.draghandle {
    margin-top: 2px;
    cursor: move;
    float: left;
    background: transparent url(../../images/visualization/draggable_horizontal.png) center center no-repeat;
    width: 10px;
    height: 12px;
}

// Highlight for element(s) being dragged@mixin  This definition must be after definition of draggable
// elements (e.g. track, group) in order to take precedence.
.dragging {
    border: 1px solid blue;
}
