// Mix-ins
@mixin borderRadius($radius) {
    -moz-border-radius: $radius;
    -webkit-border-radius: $radius;
    border-radius: $radius;
}

@mixin boxShadow($boxShadow) {
    -moz-box-shadow: $boxShadow;
    -webkit-box-shadow: $boxShadow;
    box-shadow: $boxShadow;
}

@mixin opacity($opacity) {
    $opacityPercent: $opacity * 100;
    opacity: $opacity;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=${opacityPercent})";
    filter: "alpha(opacity=${opacityPercent})";
}

@mixin wordWrap($wordWrap: break-word) {
    -ms-word-wrap: $wordWrap;
    word-wrap: $wordWrap;
}

// Variables
$black: #000000;
$grey: #999999;
$light-grey: #cccccc;
$white: #ffffff;
$near-black: #030303;
$green: #51a351;
$red: #bd362f;
$blue: #2f96b4;
$orange: #f89406;

// Styles
.toast-title {
    font-weight: bold;
}

.toast-message {
    @include wordWrap();

    a,
    label {
        color: $white;
    }

    a:hover {
        color: $light-grey;
        text-decoration: none;
    }
}

.toast-close-button {
    position: relative;
    right: -0.3em;
    top: -0.3em;
    float: right;
    font-size: 20px;
    font-weight: bold;
    color: $white;
    -webkit-text-shadow: 0 1px 0 rgba(255, 255, 255, 1);
    text-shadow: 0 1px 0 rgba(255, 255, 255, 1);
    @include opacity(1);

    &:hover,
    &:focus {
        color: $black;
        text-decoration: none;
        cursor: pointer;
        @include opacity(1);
    }
}

/*Additional properties for button version
 iOS requires the button element instead of an anchor tag.
 If you want the anchor version, it requires `href="#"`.*/
button.toast-close-button {
    padding: 0;
    cursor: pointer;
    background: transparent;
    border: 0;
    -webkit-appearance: none;
}

//#endregion

.toast-top-full-width {
    top: 0;
    right: 0;
    width: 100%;
}

.toast-bottom-full-width {
    bottom: 0;
    right: 0;
    width: 100%;
}

.toast-top-left {
    top: 12px;
    left: 12px;
}

.toast-top-right {
    top: 12px;
    right: 12px;
}

.toast-bottom-right {
    right: 12px;
    bottom: 12px;
}

.toast-bottom-left {
    bottom: 12px;
    left: 12px;
}

#toast-container {
    position: fixed;
    z-index: 999999;

    * {
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }

    > div {
        margin: 4em 0 6px;
        padding: 15px 15px 15px 50px;
        width: 300px;
        @include borderRadius(3px 3px 3px 3px);
        background-position: 15px center;
        background-repeat: no-repeat;
        @include boxShadow(0 0 12px $grey);
        color: $white;
        @include opacity(1);
    }

    > :hover {
        @include boxShadow(0 0 12px $black);
        @include opacity(1);
        cursor: pointer;
    }

    > .toast-info {
        background-image: url("data:image/png;base64,") !important;
    }

    > .toast-error {
        background-image: url("data:image/png;base64,") !important;
    }

    > .toast-success {
        background-image: url("data:image/png;base64,") !important;
    }

    > .toast-warning {
        background-image: url("data:image/png;base64,") !important;
    }

    /*overrides*/
    &.toast-top-full-width > div,
    &.toast-bottom-full-width > div {
        width: 96%;
        margin: auto;
    }
}

.toast {
    background-color: $near-black;
}

.toast-success {
    background-color: $green;
}

.toast-error {
    background-color: $red;
}

.toast-info {
    background-color: $blue;
}

.toast-warning {
    background-color: $orange;
}

/*Responsive Design*/

@media all and (max-width: 240px) {
    #toast-container {
        > div {
            padding: 8px 8px 8px 50px;
            width: 11em;
        }

        & .toast-close-button {
            right: -0.2em;
            top: -0.2em;
        }
    }
}

@media all and (min-width: 241px) and (max-width: 480px) {
    #toast-container {
        > div {
            padding: 8px 8px 8px 50px;
            width: 18em;
        }

        & .toast-close-button {
            right: -0.2em;
            top: -0.2em;
        }
    }
}

@media all and (min-width: 481px) and (max-width: 768px) {
    #toast-container {
        > div {
            padding: 15px 15px 15px 50px;
            width: 25em;
        }
    }
}
